@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .bombe-dot {
    @apply w-8 h-8 rounded-full border-2 transition-all duration-200;
  }

  .bombe-dot-selected {
    @apply bg-black border-gray-800;
  }

  .message-box {
    @apply w-10 h-10 border-2 border-gray-400 flex items-center justify-center font-mono text-lg;
  }
}

/* Additional custom styles */
.enigma-font {
  font-family: 'Courier Prime', monospace;
}